h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  font-family: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  padding: 0;
  font-family: inherit;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

textarea {
  resize: none;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f8f8f8;
}

::-webkit-scrollbar-thumb {
  background: var(--breen-color);
  border: 2px solid #f8f8f8;
  border-radius: 5px;
}

.header {
  color: var(--black-second-color);
  transition: background-color var(--time) var(--cubic);
  align-content: center;
  padding: 20px 0;
}

.header-container {
  align-items: center;
  display: flex;
}

.navigation {
  display: none;
}

.nav-item {
  align-items: center;
  display: flex;
}

.nav-item-link {
  color: var(--black-second-color);
  transition: color var(--time) var(--cubic);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.nav-item-link:hover, .nav-item-link:focus {
  color: var(--breen-color);
}

.nav-item:not(:last-child) {
  margin-right: 16px;
}

.logo {
  letter-spacing: -.64px;
  color: var(--black-second-color);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.12;
}

.logo-link {
  margin-right: auto;
}

.logo-green {
  color: var(--breen-color);
}

.basket-button {
  background-color: rgba(0, 0, 0, 0);
  align-items: center;
  margin-right: 14px;
  display: flex;
}

.basket-icon {
  fill: none;
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubic);
}

.basket-icon:hover, .basket-icon:focus {
  stroke: var(--breen-color);
}

.menu-container {
  z-index: 9;
  width: 200px;
  height: 100vh;
  background-color: var(--breen-color);
  transition: transform var(--time) var(--cubic), background-color var(--time) var(--cubic);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  z-index: index 9;
  width: 200px;
  height: 100vh;
  background-color: var(--breen-color);
  transition: transform var(--time) var(--cubic), background-color var(--time) var(--cubic);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(0);
}

.menu-toggle-icon {
  stroke: var(--black-second-color);
  transition: stroke var(--time) var(--cubic);
}

.menu-toggle-icon-close {
  stroke: var(--white-second-color);
  transition: stroke var(--time) var(--cubic), transform var(--time) var(--cubic);
}

.menu-toggle-icon:hover, .menu-toggle-icon:focus {
  stroke: var(--breen-color);
}

.menu-toggle-icon-close:hover, .menu-toggle-icon-close:focus {
  stroke: var(--black-second-color);
  transform: rotate(180deg);
}

.menu-toggle-open, .menu-toggle-close {
  background-color: rgba(0, 0, 0, 0);
  align-items: center;
  display: flex;
}

.menu-toggle-close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-list {
  flex-direction: column;
  align-items: center;
  margin-top: 200px;
  display: flex;
}

.mob-menu-item:not(:last-child) {
  margin-bottom: 16px;
}

.mob-menu-link {
  color: var(--white-second-color);
  transition: color var(--time) var(--cubic);
  font-family: Inter;
  font-size: 12px;
  font-weight: 500;
}

.mob-menu-link:hover, .mob-menu-link:focus {
  color: var(--black-second-color);
}

.mob-switcher-wrapper {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.mob-switcher-check-text {
  align-items: center;
  display: flex;
  position: relative;
}

.mob-switcher-check-text:before {
  content: "";
  width: 46px;
  height: 20px;
  transition: background-color var(--time) var(--cubic);
  background-color: #cecdcd;
  border-radius: 50px;
  justify-content: center;
  display: flex;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .6);
}

.mob-switcher-check-text:after {
  content: "";
  width: 18px;
  height: 18px;
  background-color: var(--white-second-color);
  transition: background-color var(--time) var(--cubic), transform var(--time) var(--cubic);
  border-radius: 50%;
  position: absolute;
  left: 2px;
}

.mob-switcher-check:checked + .mob-switcher-check-text:after {
  background-color: var(--whiit-first-color);
  transform: translateX(24px);
}

.mob-switcher-check:checked + .mob-switcher-check-text:before {
  background-color: var(--breen-color);
}

.js-menu-container.light-theme {
  background-color: var(--whiit-first-color);
  color: var(--black-second-color);
}

.js-menu-container.dark-theme {
  background-color: var(--black-second-color);
  color: var(--whiit-first-color);
}

.switcher-check-text {
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .switcher-wrapper {
    display: none;
  }
}

.switcher-check-text:before {
  content: "";
  width: 46px;
  height: 20px;
  transition: background-color var(--time) var(--cubic);
  background-color: #cecdcd;
  border-radius: 50px;
  justify-content: center;
  display: flex;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .6);
}

.switcher-check-text:after {
  content: "";
  width: 18px;
  height: 18px;
  background-color: var(--white-second-color);
  transition: background-color var(--time) var(--cubic), transform var(--time) var(--cubic);
  border-radius: 50%;
  position: absolute;
  left: 1px;
}

.switcher-check:checked + .switcher-check-text:after {
  background-color: var(--whiit-first-color);
  transform: translateX(26px);
}

.switcher-check:checked + .switcher-check-text:before {
